import Koa from 'koa';
import koaBody from 'koa-body';
import tickerRoutes from './routes/ticker';

const app = new Koa();

app.use(async (ctx, next) => {
  try {
    await next();
  } catch (err) {
    ctx.status = err.status || 500;
    ctx.body = err.message;
  }
});
app.use(koaBody());
app.use(tickerRoutes.routes());
app.listen(3000);
